.gallery {
  margin-bottom: 132px;
  text-align: center;
}

.gallery h3 {
  font-family: $font-head;
  font-style: normal;
  font-weight: bold;
  font-size: 34px;
  line-height: 44px;
  margin-top: 0;
  padding: 0;
  margin-bottom: 35px;
}

.gallery__follow {
  font-family: $font-text;
  font-style: normal;
  font-weight: 500;
  font-size: 16px;
  line-height: 21px;
  color: $text-color;
  margin: 0 auto;
  position: relative;

  &:hover {
    opacity: 0.5;
  }

  &::after {
    position: absolute;
    content: "";
    width: 100%;
    height: 0px;
    border: 1px solid #a87b62;
    top: 25px;
    left:0
  }
}

.gallery__images {
  display: flex;
  justify-content: space-between;
  margin-top: 70px;
}

.gallery__block {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}


@media (max-width: $tablet-width) {
  .gallery {
  }
  .gallery__follow {
  }
  .gallery__images {
  }
  .gallery__block-custom1 {
    width: 133px;
    height: 133px;
  }

  .gallery__block2 img {
    width: 231px;
    height: 240px;
  }

  .gallery__block-custom2 {
    width: 133px;
    height: 87px;
  }
}
